import { Component, OnInit, ElementRef } from '@angular/core';
import { DashbordService } from '../services/dashbord.service';
import { DuyurularService } from '../services/genel/duyurular.service';
import { DuyurularJoin } from '../models/genel/duyurular';
import { UserControlService } from '../services/usercontrol.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})

export class DashboardComponent implements OnInit {

  bgwidth: number = 1000;
  perISA: number = 0;
  perISB: number = 0;
  perISG: number = 0;
  perHV: number = 0;
  perISK: number = 0;
  perISO: number = 0;
  birISA: number = 0;
  birISB: number = 0;
  birISG: number = 0;
  birHV: number = 0;
  birISK: number = 0;
  birISO: number = 0;
  cmgizle: boolean = false;
  duyurularList: DuyurularJoin[] = [];
  constructor(
    private servis: DashbordService,
    private servisUser: UserControlService,
    private servisDuyurular: DuyurularService) {
  };

  ngOnInit() {
    this.servisDuyurular.GetDuyurularAllJoinDashbord().subscribe(data => {
      this.duyurularList = data;
    });
  }

}
